import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Map from "../components/map"
import Paranoma from "../components/paranoma"

export default ({ data }) => {
  const name = data.toilets.name
  const lat = data.toilets.lat
  const lon = data.toilets.lon
  const paranomaUrl = data.toilets.paranomaUrl
  const startingYaw = data.toilets.startingYaw

  return (
    <Layout>
      <h1>{name}</h1>
      <Map lat={lat} lon={lon} />
      <Paranoma name={name} paranomaUrl={paranomaUrl} startingYaw={startingYaw} />
      <p>Todo</p>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    toilets(fields: { slug: { eq: $slug } }) {
      name
      lat
      lon
      paranomaUrl
      startingYaw
    }
  }
`
