import React from "react"
// https://i.imgur.com/Rw0L7jC.png

class Map extends React.Component {
  constructor(props) {
    super(props)
  }

  componentWillUnmount() {
    clearInterval(this.timerID)
  }

  getGeoLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        position => {
          // console.log("yay")
          this.locationMarker.setPosition({
            lat: position.coords.latitude,
            lng: position.coords.longitude
          })

          this.accuracyRadius.setCenter({
            lat: position.coords.latitude,
            lng: position.coords.longitude
          })

          this.accuracyRadius.setRadius(position.coords.accuracy)
        },
        () => {
          //handleLocationError(true, infoWindow, map.getCenter());
        },
        { enableHighAccuracy: true }
      )
    } else {
      // Browser doesn't support Geolocation
      //handleLocationError(false, infoWindow, map.getCenter());
    }
  }

  componentDidMount() {
    this.timerID = setInterval(() => this.getGeoLocation(), 1000)

    this.map = new window.google.maps.Map(document.getElementById("map"), {
      center: { lat: this.props.lat, lng: this.props.lon },
      zoom: 17,
    })

    new window.google.maps.Marker({
      position: { lat: this.props.lat, lng: this.props.lon },
      map: this.map,
    })

    this.locationMarker = new window.google.maps.Marker({
      map: this.map,
      icon: `https://i.imgur.com/Rw0L7jC.png`,
      position: { lat: this.props.lat, lng: this.props.lon }
    })

    this.accuracyRadius = new window.google.maps.Circle({
      map: this.map,
      center: this.locationMarker.getPosition(),
      radius: 0,
      fillColor: '#00F',
      fillOpacity: 0.2,
      strokeWeight: 0
    })
  }

  render() {
    return <div id="map" style={{ width: "100%", height: 200 }} />
  }
}

export default Map
